/* global google */
import React from "react";

class DistanceRenderer extends React.Component {
    state = {
        distanceList: [],
        error: null
    };
    componentDidMount(){
        const {friends, destination} = this.props;
        var realFriends = friends.friends.filter(friend => friend.location.length > 0)
        var origins = realFriends.map( friend => JSON.parse(friend.location).geometry.location)
        
        var destinations = [JSON.parse(destination).geometry.location]
        console.log("in distance matrix renderer: ", origins, destinations)


        var distance = new google.maps.DistanceMatrixService();
        distance.getDistanceMatrix(
            {
                origins:origins,
                destinations:destinations,
                travelMode:'DRIVING',
                avoidTolls:true
            
            }, (result, status) => {
                console.log("distance response", result, status)
                if (status !== google.maps.DirectionsStatus.OK) {
                    this.setState({error: result});
                }
                if(!result) {
                    return console.log('no distances');
                }
                if (status === google.maps.DirectionsStatus.OK) {
                    const distances = result
                    for (var i=0; i < origins.length; i++) {
                        for (var j = 0; j < destinations.length; j++) {
                            var origin = distances.originAddresses[i];
                            var destination = distances.destinationAddresses[j];
                            if (distances.rows[0].elements[j].status === 'OK') {
                                var duration = distances.rows[i].elements[j].duration.value;
                                this.setState({distanceList: [...this.state.distanceList, duration]});
                                console.log("one distance from friend to dest is", duration)
                            } else {
                                console.log(destination + ' is not reachable by land from ' + origin);
                            }
                        }
                    }
                }
            }
        )
    }

    render(){
        if (this.state.error) {
            return <h1>{this.state.error}</h1>;
          }
        console.log("distance list", this.state.distanceList)
        let average = (array) => array.reduce((a, b) => a+b) / array.length;
        var duration = ""
        if(this.state.distanceList.length){
            const time = average(this.state.distanceList)
            duration = (time<3600 ? Math.ceil(time/60)+" minutes" : Math.floor(time/3600)+" hr, "+Math.ceil((time-Math.floor(time/3600)*3600)/60)+" min")
        }
        return (this.state.distanceList.length && duration)
    }
}

export default DistanceRenderer