import React, { Component } from 'react';
import Map from './Map'
import {MapFlexGrow} from '../styles'

export default class MapContainer extends Component {
   render() {      
   return(
      <MapFlexGrow>
        <Map
          hasOneDest={this.props.hasOneDest}
          hasMultipleDest={this.props.hasMultipleDest}
          //hasFriends={this.props.hasFriends}
          hasRealFriends={this.props.hasRealFriends}
          googleMapURL={"https://maps.googleapis.com/maps/api/js?key=" + process.env.REACT_APP_GOOGLE_API_KEY + "&v=3.exp&libraries=geometry,drawing,places"}
          loadingElement={<div style={{ height: `100%` }} />}
          containerElement={<div style={{ height: `100%`, width: `100%` }} />} //this is where to change the container size
          mapElement={<div style={{ height: `100%` }} />}
          destinations={this.props.destinations}
          friends={this.props.friends}
        />
      </MapFlexGrow>
   );
    
   }
};






