import styled from 'styled-components'

export const Container = styled.div`
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    background: linear-gradient(0deg, rgba(255,255,255,0.8216328767835259) 0%, 
      rgba(255,255,255,0) 0%, 
      rgba(255,255,255,0.1017449216014531) 31%, 
      rgba(255,255,255,0) 62%, 
      rgba(255,255,255,0.09894380115327384) 100%
    ),url(https://i.imgur.com/FVGKqT8.jpg);
    background-size: cover;
`
export const Title = styled.h1`
    font-family: 'Lato', sans-serif;
    font-weight: 700pt;
    font-size: 30pt;
    margin-top: 20px;
    color: black;
`

export const Underline = styled.span`
    border-bottom: 10px solid rgb(247,214,36, 0.5);
`
export const BlueUnderlineTitle = styled.h2`
  font-family: 'Lato', sans-serif;
  font-weight: 300pt;
  font-size: 24px;
  margin: 10px 0px;
  line-height: 12px;
  border-bottom: 7px solid rgb(170,218,255, 0.5);
`

export const GreenUnderlineTitle = styled.h2`
  font-family: 'Lato', sans-serif;
  font-weight: 300pt;
  font-size: 24px;
  margin: 10px 0px;
  line-height: 12px;
  border-bottom: 7px solid rgb(77,181,70, 0.5);
`


export const AppDescription = styled.h2`
  font-family: 'Lato', sans-serif;
  font-weight: 300;
  font-size: 13px;
  color: #000;
  opacity: 75%;
  width: 250px;
  margin: 5px 0px;
`
export const PaddedDiv = styled.div`
  margin: 20px 0px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
`

export const SearchContainer = styled.div`
    width: 220px;
    height: 40px;
    display:flex;
    justify-content: space-between;
    border-radius: 5px;
    /* margin-left: 60px; */
`
export const SearchInput = styled.input`
    border-radius: 5px;
    width: 100%;
    height: 100%;
    border:none;
    padding: 0px 0px 0px 30px;
    background-image: url(https://cdn2.iconfinder.com/data/icons/ios-7-icons/50/search-512.png);
    background-size: 15px;
    background-repeat: no-repeat;
    background-position: 10px 12px;
    background-color: #EBECF0;
    font-family: 'Lato', sans-serif;
    font-weight:400pt;
    &:focus{
      outline:none;
    }
`
export const PlainSearchInput = styled.input`
    /* border-radius: 5px; */
    width: 100%;
    height: 100%;
    border:none;
    border-bottom: 2px solid #393939;
    padding: 0px 0px 0px 30px;
    background-image: url(https://cdn2.iconfinder.com/data/icons/ios-7-icons/50/search-512.png);
    background-size: 15px;
    background-repeat: no-repeat;
    background-position: 10px 12px;
    /* background-color: #EBECF0; */
    font-family: 'Lato', sans-serif;
    font-weight:300;
    &:focus{
      outline:none;
    }
`


export const SearchButton = styled.button`
    border-radius: 10px;
    /* flex-grow: 1; */
    margin-left: 15px;
    width: 25%;
    color: white;
    border: none;
    background-color: black;
    outline:none;
    font-family: 'Lato', sans-serif;
    font-weight:400pt;
    &:focus{
      outline:none;
    }
    &:hover{
      cursor:pointer;
    }
`

export const NavBarContainer = styled.div`
  margin:0;
  padding: 0;
`

export const NavBarList = styled.ul`
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: transparent;
  position: fixed;
`

export const NavBarItem = styled.li`
  float: left;
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  font-family: 'Lato', sans-serif;
  font-weight:400pt;
  text-decoration: none;
  &:hover{
    cursor: pointer;
    color: lightgray;
  }
`
export const HeaderList = styled.ul`
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: black;
  position: fixed;
  width: 100vw;
  z-index:1;
`

export const CalculateContainer = styled.div`
  display:flex;
  width: 100vw;
  height: 100vh;
  /* padding-top:50px; */
  /* box-sizing: border-box; */
`
export const CalculateSidebar = styled.div`
  display:flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 30px;
  width: 250px;
  height: auto;
  background-color: white;
`
export const PlaceTitle = styled.h1`
  font-family: 'Lato', sans-serif;
  font-weight: 700pt;
  font-size: 36px;
  margin: 10px 0px;
`

export const PlaceDescription = styled.h2`
  font-family: 'Lato', sans-serif;
  font-weight: 400pt;
  font-size: 15px;
  color: #000;
  opacity: 30%;
  width: 150px;
  margin: 0px;
`

export const MapFlexGrow = styled.div`
  flex-grow: 1;
`

