export const FriendContainerStyle = styled.div`
  display:flex;
  flex-direction:column;
  flex-grow:1;
  width:100%;
`

export const FriendListStyle = styled.ul`
  list-style-type: none;
  margin: 0;
  padding: 5px;
  overflow: scroll;
  background-color: transparent;
  display: flex;
  flex-direction: column;
  height: 100px;
  flex-grow:1;
  &::-webkit-scrollbar{
    width: 0px;
    background: transparent;
  }
`

export const AddButton = styled.button`
  font-family: 'Lato', sans-serif;
  font-weight: 400pt;
  font-size: 24px;
  background-color: white;
  border: none;
  &:focus{
    outline:none;
  }
  &:hover{
    cursor:pointer;
  }
`

export const FriendItem = styled.li`
  background-color: white;
  border-radius: 5px;
  width: 100%;
  height: 80px;
  box-shadow: -2px 2px 6px rgb(0, 0, 0, 0.1);
  box-sizing: border-box;
  padding: 10px;
  margin-bottom: 10px;
`

export const FriendName = styled.p`
  margin: 0 0 5px 0;
  font-family: 'Lato', sans-serif;
  font-weight: 300pt;
`
export const FriendNameInput = styled.input`
  margin: 0;

  /*text*/
  font-family: 'Lato', sans-serif;
  font-weight: 300pt;
  font-size: 12px;

  /*input style */
  border: none;
  &:focus{
    outline:none;
  }
`

export const DeleteButton = styled.button`
  padding: 2px;
  height: 20px;
  border: none;
  border-radius: 2px;
  background-color: transparent;
  &:focus{
    outline:none;
  }
  &:hover{
    cursor:pointer;
  }
`

export const SaveButton = styled.button`
  border-radius: 5px;
  color: #393939;
  background-color: #AADAFF;
  border:none;
  font-family: 'Lato', sans-serif;
  font-size: 10px;
  padding: 3px 10px;
  height: 20px;

  &:focus{
      outline:none;
    }
  &:hover{
    cursor:pointer;
  }
`

export const DistanceContainerStyle = styled.div`
  background-color: #E8E8E8;
  border-radius: 5px;
  padding: 10px 15px;
  width: 100%;
  box-sizing: border-box;
  margin-bottom: 20px;

  /*flexbox*/
  display: flex;
  flex-direction: column;
  align-items:flex-start;
  justify-content: space-between;

`

export const BodyText = styled.p`
  font-family: 'Lato', sans-serif;
  font-size: 14px;
  color: #393939;
`

export const RowFlexContainer = styled.div`
  display:flex;
  flex-direction:row;
`
export const ColumnFlexContainer = styled.div`
  display:flex;
  flex-direction:column;
`
export const LocationTimeBold = styled.p`
  font-family: 'Lato', sans-serif;
  font-size: 18px;
  font-weight: 600;
  color: #393939;
  margin: 0;
`

export const LocationsList = styled.ul`
  list-style-type: none;
  margin: 0;
  padding: 0;
  height: 60px;

  /*scroll stuff */
  overflow:scroll;
  &::-webkit-scrollbar{
    width: 0px;
    background: transparent;
  }
`

export const LocationsListItem = styled.li`
  padding: 5px 12px;
  margin: 10px 0 10px 5px;
  width: 95%;
  box-sizing: border-box;
  background-color: white;
  border-radius:5px;

  /*text*/
  font-family: 'Lato', sans-serif;
  font-size: 8px;
  font-weight: 300;
  color: #393939;

  /*flexbox*/
  display:flex;

`

export const IconButton =  styled.button`
  border: none;
  border-radius: 100px;
  width: 50px;
  height: 50px;
  &:focus{
    outline:none;
  }
  &:hover{
    cursor:pointer;
  }
`