import React from 'react'
import {nanoid} from 'nanoid'
import { GreenUnderlineTitle, AddButton, FriendItem, FriendListStyle, 
  FriendContainerStyle, FriendName, FriendNameInput, DeleteButton, 
  BodyText} from '../styles'
import SearchBoxContainer from './Searchbox'
import DeleteImg from '../images/delete.png'
//port { parseSync } from '@babel/core'

const FriendContainer = ({form, setForm}) => {
  //const [form, setForm] = useReducer(formReducer, {friends: []})
  return(
    <FriendContainerStyle>
    <div style={{display:"flex", justifyContent:"space-between"}}>
    <GreenUnderlineTitle>Friends</GreenUnderlineTitle>
    <AddButton onClick={()=>setForm(form['friends'].push({id:nanoid(), name: "Sample Name", location: "", time: ""}))}>+</AddButton> 
    </div>
    {form.friends.length>0? <FriendList 
        // t={ form['tasks'].filter(task => task.text.includes(searchText))} use later when we have categories
        form={form} setForm={setForm} >
        </FriendList> : <NoFriends/>}
    </FriendContainerStyle>
  )
}

const FriendList = ({form, setForm}) => (
    <FriendListStyle>
        {form['friends'].map((task) => 
      <Friend key={task.id} {...task} form={form} setForm={setForm} />)}
    </FriendListStyle>
)

const Friend = ({id, name, location, distance, form, setForm}) => { //replace with ...friend
    //const [places, setPlaces] = useState("");
    return(
      <FriendItem>
          <div style={{display:"flex", justifyContent:"space-between", padding:"0 0px 9px 0"}}>
            <FriendNameInput type="text" placeholder="Name"></FriendNameInput>  
            <DeleteButton>
                  <img src={DeleteImg} alt="Delete" style={{width: "15px"}} onClick={()=>setForm({friends: form['friends'].filter(friend => friend.id !== id)})}></img>
                  </DeleteButton>
          </div>
          
          <SearchBoxContainer value={form} setValue={(newPlace) => setForm({type: "NEW_LOCATION", id: id, location: newPlace})} 
            style={{height: "25px", padding: "0 0 0 20px", backgroundPosition: "0px", backgroundSize: "13px", 
              width: "190px", backgroundColor: "transparent", borderBottom: "1px solid #393939", borderRadius: "0px"}} isFriendAddresses>
            </SearchBoxContainer>

            <FriendName style={{margin: "-4px 0 0 0", fontSize: "15px"}}>{distance}</FriendName>

      </FriendItem>
    )
  }

const NoFriends = () => (
<BodyText>
  Add some friends!
</BodyText>
)

export default FriendContainer

