import React from "react";
// import ReactDOM from "react-dom";
import {SearchInput} from '../styles'

const { compose, withProps, lifecycle } = require("recompose");
const {
  withScriptjs,
} = require("react-google-maps");
const { StandaloneSearchBox } = require("react-google-maps/lib/components/places/StandaloneSearchBox");

const SearchBoxContainer = compose(
  withProps({
    googleMapURL: "https://maps.googleapis.com/maps/api/js?key=" + process.env.REACT_APP_GOOGLE_API_KEY + "&v=3.exp&libraries=geometry,drawing,places",
    loadingElement: <div style={{ height: `100%` }} />,
    containerElement: <div style={{ height: `400px` }} />,

  }),
  lifecycle({
    componentWillMount() {
      const refs = {}

      this.setState({
        places: [],
        onSearchBoxMounted: ref => {
          refs.searchBox = ref;
        },
        onPlacesChanged: () => {
          const places = refs.searchBox.getPlaces();
          
          this.setState({
            places,
          });

          if(places.length === 0)
            return;

          this.props.setValue(JSON.stringify(places[0])); 
          console.log("in places_changed w state "+this.props.value);
        },
      })
    },
  }),
  withScriptjs  
)(props =>
  <div data-standalone-searchbox="" style={{width: '100%', height: '100%'}}> 
    <StandaloneSearchBox
      ref={props.onSearchBoxMounted}
      bounds={props.bounds}
      onPlacesChanged={props.onPlacesChanged}
    >
      <SearchInput type="text" placeholder={props.isFriendAddresses ? "Enter an address" : "Search for a place"} style={props.style || {}}></SearchInput>
    </StandaloneSearchBox>
  </div>
);

export default SearchBoxContainer;