import React, {useState, useRef} from 'react'
import DistanceRenderer from './DistanceRenderer'
import { SaveButton, BlueUnderlineTitle, DistanceContainerStyle, FriendName, BodyText, 
    RowFlexContainer, ColumnFlexContainer, LocationTimeBold, AppDescription, LocationsList, LocationsListItem} from '../styles'

//var distance = require('google-distance-matrix');
const DistanceContainer = ({hasRealFriends, hasOneDest, hasMultipleDest, friends, destination, setFriends}) => {
    const [locations, setLocations] = useState([])
    const [time, setTime] = useState(0)
    const distanceRenderer = useRef()

    if(!hasRealFriends || !hasOneDest){
        return <BodyText>No meet up spots yet! Add a destination and a friend to get started.</BodyText>
    }
    else{
        return (
        
        <DistanceContainerStyle>
            
            
            <BlueUnderlineTitle style={{fontSize:"22px"}}>Average Driving Time</BlueUnderlineTitle>
            <RowFlexContainer style={{width: "100%", margin: "8px 0px", alignItems: "center"}}>
                <ColumnFlexContainer style={{width:"90%"}}>
            <LocationTimeBold>{hasRealFriends && hasOneDest && 
                <DistanceRenderer ref={distanceRenderer} key={new Date().getTime()} friends={friends} setFriends={setFriends} time={time} setTime={setTime} destination={destination}></DistanceRenderer>}</LocationTimeBold>
            <AppDescription style={{width:"80%"}}>{hasOneDest && JSON.parse(destination).formatted_address}</AppDescription>
            </ColumnFlexContainer>
            <SaveButton onClick={() => setLocations([...locations, {"name": JSON.parse(destination).name, "address":JSON.parse(destination).formatted_address, "time": getDistanceFromList(distanceRenderer.current.state.distanceList)}])}>Save</SaveButton>
            </RowFlexContainer>
            {locations.length ? 
                <div style={{width: "100%"}}>
                    <FriendName style={{fontSize: "10px", margin: "5px 0px"}}>Compare</FriendName>
                    <LocationsList>{locations.map(loc => <LocationsItem name={loc.name} address={loc.address} time={loc.time} locations={locations} setLocations={setLocations} distanceRenderer={distanceRenderer}></LocationsItem>)}</LocationsList>
                </div>
            : <></>}
        </DistanceContainerStyle>
        )
    }
}

const average = (array) => array.reduce((a, b) => a+b) / array.length;

const getDistanceFromList = (distanceList) => {
    const time = average(distanceList)
    return (time<3600 ? Math.ceil(time/60)+" min" : Math.floor(time/3600)+" hr, "+Math.ceil((time-Math.floor(time/3600)*3600)/60)+" min")
}

const LocationsItem = ({name, address, time, locations, setLocations, distanceRenderer}) => (
    <LocationsListItem>
        <ColumnFlexContainer style={{width:"75%"}}>
            <LocationTimeBold style={{fontSize: "12px"}}>{name}</LocationTimeBold> 
            <>{address}</>
        </ColumnFlexContainer>
        <RowFlexContainer style={{fontSize: "12px", alignItems: "center"}}>
            {/*console.log("distance renderer value:", distanceRenderer.current)*/}
            {/*setLocations([...locations, {"name": name, "address": address, "time": getDistanceFromList(distanceRenderer.current.state.distanceList)}])*/}
            {/*distanceRenderer.current.state.distanceList.length && getDistanceFromList(distanceRenderer.current.state.distanceList)*/}
            {time}
        </RowFlexContainer>
        
    </LocationsListItem>

)


export default DistanceContainer
