/* global google */
import React from "react";
import {Marker} from 'react-google-maps';

class NearbySearch extends React.Component {
    state = {
        placesList: []
    }
    componentDidMount(){
        const {center, type} = this.props;
        var request = {
            location: center,
            radius: '500',
            type: [type]
          };
          console.log("in nearby search", type(this))
          var service = new google.maps.places.PlacesService(this);
          service.nearbySearch(request, (result, status) => {
              if (status === google.maps.places.PlacesServiceStatus.OK) {
                for (var i = 0; i < result.length; i++) {
                    this.setState({placesList: [...this.state.placesList, result[i]]});
                }
          }});
    }
    render(){
        //this.props.setPlacesClicked(false);
        return (this.state.placesList.length && 
            this.state.placesList.map(place => 
                <Marker key={place.place_id} location={place.geometry.location}></Marker>
            )
        )
    }
}

export default NearbySearch