import React from 'react';
import { withScriptjs, withGoogleMap, GoogleMap, Marker } from 'react-google-maps';
import MapDirectionsRenderer from './MapDirectionsRenderer'
import NearbySearch from './NearbySearch';

const Map = withScriptjs(withGoogleMap((props) => {
    var placeMarkers = [];
    var friendMarkers = [];
    console.log("is initial state?", !(props.hasOneDest || props.hasMultipleDest || props.hasRealFriends))
    console.log("is one destination? ", props.hasOneDest && !props.hasMultipleDest)
    if(props.hasOneDest && Object.keys(JSON.parse(props.destinations)).length){
        placeMarkers = props.hasOneDest ? <Marker position={JSON.parse(props.destinations).geometry.location}></Marker> : 
            JSON.parse(props.destinations).map( dest => <Marker
                key={dest.place_id}
                position={dest.geometry.location}
                //icon={props.destinations.length===1? } //fix this to change the icon based on store or restaurant etc.
            />);
    }
    if (props.hasRealFriends && props.friends.length){
        friendMarkers = props.friends.map( friend => {
            if(friend.location.length){
                return (
                <Marker
                    key={friend.id}
                    position={JSON.parse(friend.location).geometry.location}
                />)
            }
            return undefined
        });
    }
  
  
  return (
    
    <GoogleMap
      key={new Date().getTime()}
      defaultZoom={12}
      center={{lat: 38.895, lng: -77.036}}
      >
      {!props.isInitialState && props.destinations.length && !props.hasRealFriends && placeMarkers}
      {!props.isInitialState && props.hasRealFriends && !(props.destinations.length > 0) && friendMarkers }
      {!props.isInitialState && !props.hasOneDest && props.hasMultipleDest && 
        <NearbySearch center={{lat: 38.895, lng: -77.036}} type={"restaurant"} setPlacesClicked={props.setPlacesClicked}></NearbySearch>}
      {!props.isInitialState && props.hasOneDest && props.hasRealFriends && 
        props.friends.map(friend => friend.location.length !== 0 && <MapDirectionsRenderer
            dest={JSON.parse(props.destinations).geometry.location}
            origin={JSON.parse(friend.location).geometry.location}
            // travelMode={google.maps.TravelMode.DRIVING} //default is driving anyway
          />)
        }
    </GoogleMap>
    
  );

  }
))

export default Map
