import React, { useState, useReducer, useEffect} from 'react'
import MapContainer from './MapContainer'
import {CalculateContainer, CalculateSidebar, AppDescription, Title, FriendName, Underline, 
  SearchContainer, PaddedDiv, IconButton} from '../styles'
import FriendContainer from './FriendContainer'
import SearchBoxContainer from './Searchbox'
import DistanceContainer from './DistanceContainer'

const formReducer = (prevState, action) => {
    console.log("formReducer called "+JSON.stringify(action));
    if(!action){
      return { ...prevState }
    }
    switch(action.type){
      case 'NEW_NAME':
        var i;
        var newState = {...prevState}
        for (i = 0; i < newState['friends'].length; i++) {
            if(newState['friends'][i].id === action.id)
            newState['friends'][i].name = action.name;
        }
        console.log("new friend name: ", action.name)
        return newState;
      case 'NEW_LOCATION':
          console.log("new location case in formReducer")
          var i;
          var newState = {...prevState}
          for (i = 0; i < newState['friends'].length; i++) {
              if(newState['friends'][i].id === action.id)
                newState['friends'][i].location = action.location;
          }
          //console.log(prevState['friends']);
          //console.log(prevState);
          console.log("new location coordinates", JSON.parse(action.location).geometry.location)
          return newState;
      case 'NEW_TIME':
          var i;
          var newState = {...prevState}
          for (i = 0; i < newState['friends'].length; i++) {
              if(newState['friends'][i].address === action.address)
                newState['friends'][i].time = action.time;
          }
          console.log("change time in form reducer", newState['friends'])
        return newState;
      default:
        return { ...prevState, ...action }
    }
  }

const Calculate = (props) => {
    //const [addr, setAddr] = useState([])
    const [placesClicked, setPlacesClicked] = useState(false)
    const [destination, setDestination] = useState("") //hold latitude and longitude
    //const [friends, setFriends] = useState([])
    const [friends, setFriends] = useReducer(formReducer, {friends: []})

    useEffect(() => {
      const script = document.createElement('script');
    
      script.src = "https://buttons.github.io/buttons.js";
      script.async = true;
      script.defer = true;

      document.body.appendChild(script);
    
      return () => {
        document.body.removeChild(script);
      }
    }, []);

    return(
    <>
        <CalculateContainer>

                <CalculateSidebar>
                    <PaddedDiv>
                    <Title><Underline>Where to meet?</Underline></Title>
                    <AppDescription> An app for minimizing driving times when meeting in a group. </AppDescription>
                    </PaddedDiv>
                    <PaddedDiv>
                    <FriendName style={{fontSize: "10px"}}>Location</FriendName>
                    <SearchContainer>
                        <SearchBoxContainer value={destination} setValue={(newPlace) => {setDestination(newPlace); console.log("destination just updated to", destination);}}></SearchBoxContainer>
                    </SearchContainer>

                    </PaddedDiv>
                    <PaddedDiv style={{flexDirection: "row", justifyContent: "space-between", width: "100%"}}>
                      <IconButton style={{backgroundColor: "#C5E9C5"}} onClick={()=>setPlacesClicked(true)}></IconButton>
                      <IconButton style={{backgroundColor: "#AADAFF"}} onClick={()=>setPlacesClicked(true)}></IconButton>
                      <IconButton style={{backgroundColor: "#FF9E67"}} onClick={()=>setPlacesClicked(true)}></IconButton>
                    </PaddedDiv>
                    <PaddedDiv>
                      <a class="github-button" href="https://github.com/lingelizabeth/map-meetup" 
                        data-icon="octicon-star" data-size="large" data-show-count="true" 
                        aria-label="Star lingelizabeth/map-meetup on GitHub">Star</a>
                    </PaddedDiv>
                </CalculateSidebar>
                <CalculateSidebar>
                  <DistanceContainer
                    hasRealFriends={friends.friends.filter(friend => friend.location.length > 0).length > 0}
                    friends={friends}
                    setFriends={setFriends}
                    destination={destination} 
                    hasOneDest={(destination.length !== 0)} 
                    hasMultipleDest={placesClicked}
                  >
                  </DistanceContainer>
                    {/* <PlaceTitle style={{fontSize: '36px'}}>{JSON.parse(props.location.data).name}</PlaceTitle>
                    {console.log(props.location.data)}
                    <PlaceDescription> {JSON.parse(props.location.data).formatted_address}</PlaceDescription> */}
                    <FriendContainer destination={destination}
                        form={friends} setForm={setFriends} 
                    >
                    </FriendContainer>
                </CalculateSidebar>

        <MapContainer 
            //hasFriends={friends.friends.length !== 0}
            hasRealFriends={friends.friends.filter(friend => friend.location.length > 0).length > 0}
            friends={friends.friends}
            destinations={destination} 
            hasOneDest={(destination.length !== 0)} 
            hasMultipleDest={placesClicked}
            setPlacesClicked={setPlacesClicked}></MapContainer>
        </CalculateContainer>

    </>
    )
}
export default Calculate